
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
data(){
return {
tabActiveIndex:0,
approvalData:[],
approvalHistoryData:[]
}

},
created(){
this.getApprovalData()
},
methods:{
onItemClick(index){
this.tabActiveIndex=index
},
turnToDtl(){
this.$router.push({name:'approvalDtl',params:{id:12121221}})
},
getApprovalData(){
this.$vux.loading.show({
text: 'Loading'
})

let self=this
axios.all([
axios.get('https://mp.orancat.com/mock/myApproval'),
axios.get('https://mp.orancat.com/mock/myApprovalHistory')
])
.then(axios.spread(function (res1,res2,res3) {
self.$vux.loading.hide()
if(res1.data.status==0){
self.approvalData=res1.data.data;
}else{
self.$vux.alert.show({
title:'提示',
content:"请求错误"
})
return
}
if(res2.data.status==0){
self.approvalHistoryData=res2.data.data;
}else{
self.$vux.alert.show({
title:'提示',
content:"请求错误"
})
return
}
}));
console.log(this.approvalHistoryData,222)















}
},
components: {
XHeader,
Tab,
TabItem
}
}



