
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
data(){
return {
approvalInfo:null,
commentList:null
}

},
created(){
this.getApprovalInfo().then(()=>{
this.getCommentData()
})
},
methods:{
getApprovalInfo(){
this.$vux.loading.show({
text: 'Loading'
})

let self=this
return axios({
method:'Get',
url:'https://mp.orancat.com/mock/getApprovalDtl'
}).then((res)=>{
this.$vux.loading.hide()
console.log(res.data,'getApprovalInfo')
if(res.data.status==0){
this.approvalInfo=res.data.data;
}else{
this.$vux.alert.show({
title:'提示',
content:"请求错误"
})
}
})

},
getCommentData(){
this.$vux.loading.show({
text: 'Loading'
})

let self=this
axios({
method:'Get',
url:'https://mp.orancat.com/mock/commentList'
}).then((res)=>{
this.$vux.loading.hide()

if(res.data.status==0){
this.commentList=res.data.data;
}else{
this.$vux.alert.show({
title:'提示',
content:"请求错误"
})
}
})
},
operation(e){

let operaType=e.target.dataset.type;

this.$router.push({
path: this.$route.fullPath+'/publish',
query: {
operaType: operaType
}
})
}
},

components: {
XHeader
}
}



