import Vue from 'vue'
import Router from 'vue-router'

import homePage from '@/components/homePage/homePage'
import goOut from '@/components/goOut/goOut'
import approval from '@/components/approval/approval'
import approvalDtl from '@/components/approvalDtl/approvalDtl'
import userPage from '@/components/userPage/userPage'
import attendance from '@/components/userPage/attendance/attendance'
import inventory from '@/components/userPage/inventory/inventory'
import handbook from '@/components/handbook/handbook'
import publish from '@/components/approvalDtl/publish/publish'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/homePage'
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage,
      meta:{auth:true}
    },
    {
      path: '/goOut',
      name: 'goOut',
      component: goOut,
      meta:{title:'外出'},
    },
    {
      path: '/approval',
      name: 'approval',
      component: approval,
      meta:{title:'我审批的'},
      children:[
        {
          path:':id',
          name:'approvalDtl',
          component:approvalDtl,
          meta:{title:'申请详情'},
          children:[
            {
              path:'publish',
              name:'publish',
              component:publish,
              meta:{title:'审批意见'},
            }
          ]
        }
      ]
    },
    {
      path: '/userPage',
      name: 'userPage',
      component: userPage,
      meta:{title:'我的主页'},
      children:[
        {
          path:'attendance/:id',
          name:'attendance',
          component: attendance,
          meta:{title:'我的考勤记录'}
        },
        {
          path:'inventory/:id',
          name:'inventory',
          component: inventory,
          meta:{title:'table'}
        }
      ]
    },
    {
      path: '/handbook',
      name: 'handbook',
      component: handbook,
      meta:{title:'员工手册'}
    },
  ]
})



