import Vue from 'vue'
import App from './App'
import router from './router'
import 'babel-polyfill'
import fastclick from 'fastclick'
import store from './store'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import '../static/css/app.css'
import '../static/css/font.css'
import VConsole from 'vconsole'

import VueLoading from './plugin/loading'
import Toast1 from './plugin/toast'
import alert1 from './plugin/alert'
Vue.use(VueLoading)
Vue.use(Toast1)
Vue.use(alert1)

Vue.use(require('vue-wechat-title'))
let vConsole = new VConsole()



Vue.component(Datetime.name, Datetime);
Vue.component(Group.name, Group);
Vue.component(Toast)
Vue.use(AlertPlugin);
Vue.use(LoadingPlugin);

fastclick.attach(document.body)




Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})



