export default {
    data () {
        return {
            name:'ec-alert',
            show: false,
            title: '提示s',
            content: '',
            submitText: '确定',
            theme:false,
            color:'#20a0ff',
            animation:'ec'
        }
    },
    computed: {},
    mounted () {
        if(!this.theme){
            this.color='';
        }
    },
    methods: {
        success () {
            this.show = false;
        }
    }
}



